import _ from "lodash";
import queryString from "query-string";

const parseOrderItem = item => {
  try {
    const { ext } = JSON.parse(item);
    console.log("parseOrderItem ext: ", ext);
    const result = ext.split("\t").map(i => {
      const parse = ([a1, a2]) => ({ title: a1, description: a2 });
      return parse(i.split(":"));
    });
    return result;
  } catch (error) {
    return [];
  }
};

const getParametersWithLocation = location => {
  const { search } = location;
  const { appid, oauthtoken, paymentott } = queryString.parse(search);

  return {
    appid,
    oauthtoken,
    paymentott
  };
};

export default {
  parseOrderItem,
  getParametersWithLocation
};
