import React from "react";

import { Navbar } from "react-bootstrap";
import Formatters from "@zalopay/formatters";
import "./index.scss";

import ItemList from "./item-list";
import ZPPasscodeInputModal from "./zp-passcode-input";
import PMCItem from "./pmc-item";

const ZPNavbar = ({
  children,
  title = "Thanh toán",
  leftButtonTitle,
  onLeftButtonPress,
  rightButtonTitle,
  onRightButtonPress
}) => (
  <Navbar className="navbar-fixed-top navbar-bg-header navbar-default">
    <div className="zp-navbar-wrapper">
      <Navbar.Toggle onClick={onLeftButtonPress} className="navbar-button">
        {leftButtonTitle}
      </Navbar.Toggle>
      <Navbar.Brand className="navbar-title">{title}</Navbar.Brand>
      <Navbar.Toggle onClick={onRightButtonPress} className="navbar-button">
        {rightButtonTitle}
      </Navbar.Toggle>
    </div>
  </Navbar>
);

const Amount = ({ amount }) => (
  <p className="amount-title">{`${Formatters.decimalString(amount)} VND`}</p>
);

const OrderItem = ({ title, description }) => (
  <p className="order-item">
    <span className="order-item-title">{title}</span>
    <span className="order-item-description">{description}</span>
  </p>
);

export { ZPNavbar, Amount, OrderItem };
export { ItemList, ZPPasscodeInputModal, PMCItem };
