import Formatters from "@zalopay/formatters";

import Helper from "../helper";
import ACTION_TYPE from "../actions/action-type";

import {
  SOURCE_MONEY_ID,
  INPUT_PASSCODE_MODE,
  PMC_ID,
  PMC_IMAGE,
  TRANSACTION_STATUS
} from "../constants";

const initialState = {
  appid: 1,
  oauthtoken: undefined,
  paymentott: undefined,

  order: undefined,
  lockInputPasscode: false,
  passcodeError: "",
  transactionStatus: TRANSACTION_STATUS.NONE
};

const APP_NAME_TITLE = "Nhà cung cấp";

const FEE_TITLE = "Phí giao dịch";

export default (state = initialState, action = {}) => {
  const { type = "", payload } = action;
  switch (type) {
    case "SET_BASIC_INFO": {
      const { appid, oauthtoken, paymentott } = payload;
      return {
        ...state,
        appid,
        oauthtoken,
        paymentott
      };
    }

    case "UPDATE_TRANSACTION_STATUS":
      return {
        ...state,
        transactionStatus: payload
      };

    case "LOCK_INPUT_PASSCODE":
      return {
        ...state,
        lockInputPasscode: !!payload
      };
    case "UPDATE_INPUT_PASSCODE_ERROR":
      return {
        ...state,
        passcodeError: payload || ""
      };

    case ACTION_TYPE.SET_ORDER_INFO: {
      console.log("ACTION_TYPE.SET_ORDER_INFO", payload);
      const { order, pmcappinfo, userbalance } = payload;

      let { pmcs, info } = pmcappinfo;

      const { appname } = info;

      const { amount, description, item } = order;
      const items = Helper.parseOrderItem(item);
      console.log("items: ", items);

      items.push({
        title: FEE_TITLE,
        description: "Miễn phí"
      });

      items.unshift({
        title: APP_NAME_TITLE,
        description: appname
      });
      pmcs = pmcs.map(pmc => {
        const { pmcid, pmcname } = pmc;
        if (pmcid === PMC_ID.ZALOPAY) {
          return {
            ...pmc,
            userbalance,
            image: PMC_IMAGE[pmcid],
            pmcname: `${pmcname}: <b>${Formatters.decimalString(
              userbalance
            )}</b> VND`
          };
        }
        return pmc;
      });
      /* select pmc source */
      const selectedPMC = pmcs.find(pmc => {
        const { pmcid } = pmc;
        if (pmcid === PMC_ID.ZALOPAY) {
          return true;
        }
        return false;
      });

      return {
        ...state,
        amount,
        description,
        items,
        order: payload,
        selectedPMC
      };
    }
    default:
      return state;
  }
};
