import React from "react";
import { bindActionCreators } from "redux";

import { connect } from "react-redux";
import { Button } from "react-bootstrap";

import {
  Amount,
  OrderItem,
  ZPPasscodeInputModal,
  PMCItem
} from "../../components";
import Loading from "../../components/loading";
import "./index.scss";
import Actions from "../../actions";
import Helper from "../../helper";

const CONFIRM_BUTTON_TITLE = "Xác Nhận Thanh Toán";

const ZALOPAY_AUTHENTICATION_TITLE = "Giao dịch được chứng thực bởi ZaloPay";

class PaymentConfirmation extends React.PureComponent {
  state = {
    inputPasscode: false
  };
  componentDidMount() {
    const { location, match } = this.props;
    console.log("location", location);
    const { actions } = this.props;
    actions.startup({ location, match });
  }

  confirmButtonClick = () => {
    console.log("confirmButtonClick");
    const { actions } = this.props;
    actions.showInputPasscode(true);
    setTimeout(() => {
      this.focusInputPassCode();
    }, 500);
  };
  handleInputCodeCompleted = pin => {
    console.log("handleInputCodeCompleted", pin);
    const { location } = this.props;
    console.log("handleInputCodeCompleted", location);

    const { actions } = this.props;

    actions.submitOrder({
      location,
      pin,
      chargeinfo: ""
    });
  };
  handleCloseInputPasscodeMode = () => {
    const { actions } = this.props;
    actions.showInputPasscode(false);
  };
  focusInputPassCode() {
    const input = document.getElementById("zp-input-digit");
    input && input.focus();
  }

  renderContentFooter() {
    return (
      <div className="footer-comfirm-button">
        <Button className="pay-button" onClick={this.confirmButtonClick}>
          {CONFIRM_BUTTON_TITLE}
        </Button>
        <p className="authentication-title">{ZALOPAY_AUTHENTICATION_TITLE}</p>
      </div>
    );
  }
  handleChangeSource = source => {
    const { actions } = this.props;
    actions.changeSource(source);
  };

  handleClose = () => {
    const { actions } = this.props;
    actions.showInputPasscode(false);
  };

  renderPlaceholder() {
    // return <Loading />;
    const { showInputPasscode } = this.props;

    return (
      <div className="detail-wrapper">
        {this.renderContentFooter()}
        <ZPPasscodeInputModal
          show={showInputPasscode}
          onHide={this.handleCloseInputPasscodeMode}
          keyboard={false}
          onEscapeKeyDown={() => {}}
          onInputCompleted={this.handleInputCodeCompleted}
        />
      </div>
    );
  }

  render() {
    const { amount, description, items } = this.props;
    const { order } = this.props;
    if (!order) {
      return this.renderPlaceholder();
    }
    const { selectedPMC, showInputPasscode } = this.props;

    return (
      <div className="detail-wrapper">
        <div className="body">
          <div className="order-header">
            <Amount amount={amount} />
            <p className="description">{description}</p>
          </div>
          <div className="item-list">
            {items.map((item, index) => (
              <OrderItem {...item} key={index.toString()} />
            ))}
          </div>
          <PMCItem {...selectedPMC} checked={true} />
        </div>
        {this.renderContentFooter()}

        <ZPPasscodeInputModal
          show={showInputPasscode}
          onHide={this.handleCloseInputPasscodeMode}
          keyboard={false}
          onEscapeKeyDown={() => {}}
          onInputCompleted={this.handleInputCodeCompleted}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  ...state.orderInfo,
  ...state.paymentInfo,
  ...state.passcode
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(Actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PaymentConfirmation);
