import React from "react";

import { CLOSE_BUTTON_TITLE } from "../../constants";

import "./index.scss";

const FORGOT_PASSWORD_TITLE = "Hướng dẫn lấy lại mật khẩu thanh toán";
const FORGOT_PASSWORD_DESCRIPTION =
  "Để đảm bảo an toàn cho chính bạn, việc lấy lại mật khẩu thanh toán cần thực hiện trên ứng dụng ZaloPay.";
const FORGOT_PASSWORD_ACTION = "Bạn cần thực hiện";

const FORGOT_PASSWORD_TWO_STEP =
  "Đăng nhập nhanh ZaloPay từ các thông tin đã đăng ký với Zalo.";

const FORGOT_PASSWORD_THREE_STEP_HTML = `Truy cập <b>Cá nhân</b> -> <b>Thiết lập bảo vệ tài khoản</b> <span style="display: inline-block">-></span> <b>Quên mật khẩu.</b>`;

const PAY_GUIDE_DESCRIPTION =
  "Chỉ 1 lần tải và xác thực, bạn còn được nhận thông báo về kết quả giao dịch, xem lại giao dịch và các tiện ích khác trên ứng dụng ZaloPay.";

const PAY_GUIDE_TITLE = "Hướng dẫn thực hiện";
const PAY_GUIDE_THREE_STEP = "Truy cập đơn hàng đã đặt để tiếp tục thanh toán.";

const DOWNLOAD_AND_INSTALL = "Tải và cài đặt ứng dụng ZaloPay.";
const GUIDE_LOGIN =
  "Đăng nhập nhanh ZaloPay từ các thông tin đã đăng ký với Zalo.";

const ANDROID_STORE_URL =
  "https://play.google.com/store/apps/details?id=vn.com.vng.zalopay";
const IOS_STORE_URL =
  "https://apps.apple.com/us/app/zalo-pay-thanh-toan-trong/id1112407590";

const DOWNLOAD_ZALOPAY = "Tải Ứng Dụng";

const PointWrapper = ({ index, children, isLast }) => (
  <div className="point-wrapper">
    <div className={`left`}>
      <div className={`circle ${isLast && "circle-last"}`}>{index}</div>
    </div>
    <div className="right">{children}</div>
  </div>
);

const DownloadItems = () => (
  <div className="downloads">
    <a href={IOS_STORE_URL}>
      <img
        className="first-icon"
        alt=""
        src={require("../../resources/download-applestore.svg")}
      />
    </a>
    <a href={ANDROID_STORE_URL}>
      <img
        className="second-icon"
        alt=""
        src={require("../../resources/download-playstore.svg")}
      />
    </a>
  </div>
);

class PayGuidePage extends React.PureComponent {
  handleBottomButtonClick = () => {
    const { history } = this.props;
    history.push(`/forgot-password`);
    console.log("PayGuidePage");
  };
  render() {
    return (
      <div className="page forgot-password">
        <div className="body">
          <p className="small-description-text title">
            {PAY_GUIDE_DESCRIPTION}
          </p>
          <p className="description-text sub-title">{PAY_GUIDE_TITLE}</p>
          <div className="box">
            <PointWrapper index={1}>
              <p>{DOWNLOAD_AND_INSTALL}</p>
              <DownloadItems />
            </PointWrapper>
            <PointWrapper index={2}>
              <p>{GUIDE_LOGIN}</p>
            </PointWrapper>
            <PointWrapper index={3} isLast={true}>
              <p>{PAY_GUIDE_THREE_STEP}</p>
            </PointWrapper>
          </div>
        </div>
        <footer>
          <button
            className="close-button normal-button"
            onClick={this.handleBottomButtonClick}
          >
            {DOWNLOAD_ZALOPAY}
          </button>
        </footer>
      </div>
    );
  }
}

class ForgotPasswordPage extends React.PureComponent {
  handleBottomButtonClick = () => {
    console.log("ForgotPasswordPage");
    const { history } = this.props;
    history.goBack();
  };
  render() {
    return (
      <div className="page forgot-password">
        <div className="body">
          <img
            className="icon"
            src={require("../../resources/art-change-pass.svg")}
            alt=""
          />
          <p className="bold-big-text title">{FORGOT_PASSWORD_TITLE}</p>
          <p className="medium-text">{FORGOT_PASSWORD_DESCRIPTION}</p>
          <p className="bold-medium-text sub-title">{FORGOT_PASSWORD_ACTION}</p>
          <div className="box">
            <PointWrapper index={1}>
              <p>{DOWNLOAD_AND_INSTALL}</p>
              <DownloadItems />
            </PointWrapper>

            <PointWrapper index={2}>
              <p>{FORGOT_PASSWORD_TWO_STEP}</p>
            </PointWrapper>
            <PointWrapper index={3} isLast={true}>
              <p
                dangerouslySetInnerHTML={{
                  __html: FORGOT_PASSWORD_THREE_STEP_HTML
                }}
              />
            </PointWrapper>
          </div>
        </div>
        <footer>
          <button
            className="close-button normal-button"
            onClick={this.handleBottomButtonClick}
          >
            {CLOSE_BUTTON_TITLE}
          </button>
        </footer>
      </div>
    );
  }
}

export { PayGuidePage, ForgotPasswordPage };
