import axios from "axios";
import CryptoJS from "crypto-js";
import QueryString from "query-string";

axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";

if (process.env.NODE_ENV !== "development") {
  axios.defaults.baseURL = "https://qczpm.zalopay.com.vn/colossus";
}

// axios.defaults.baseURL = "https://qczpm.zalopay.com.vn/colossus";

// signParams: ["appid", "userid", "amount", "apptransid", "embeddata", "item", "description", "mac", "apptime", "clientid", "clientkey"]

var config = {
  hashFn: hashFn("sha256"),
  clientID: 1,
  clientKey: "abc123",
  signParams: [
    "appid",
    "userid",
    "amount",
    "apptransid",
    "embeddata",
    "item",
    "description",
    "mac",
    "apptime",
    "clientid",
    "clientkey"
  ]
};

function prepareParamsMap(config, params) {
  const paramsMap = {};
  console.log("params ", typeof params, params);
  Object.keys(params).forEach(key => {
    paramsMap[key] = params[key];
  });

  paramsMap["clientid"] = config.clientID;
  paramsMap["clientkey"] = config.clientKey;

  return paramsMap;
}

function hashFn(algorithm) {
  switch (algorithm) {
    case "sha256":
      return CryptoJS.SHA256;
    case "hmac-sha1":
      return CryptoJS.HmacSHA1;
    case "hmac-sha256":
      return CryptoJS.HmacSHA256;
    case "hmac-sha512":
      return CryptoJS.HmacSHA512;
    default:
      return null;
  }
}

function computeSig(config, params) {
  const paramsMap = prepareParamsMap(config, params);

  console.log("paramsMap ", paramsMap);
  const signParamsArr = config.signParams.map(o => {
    return paramsMap[o];
  });
  console.log("compose string ", signParamsArr.join("|"));
  const hashStr = config.hashFn(signParamsArr.join("|"));
  console.log("hashStr ", hashStr.toString());

  return hashStr.toString();
}

axios.interceptors.response.use(
  function(response) {
    // Do something with response data
    console.log("resp", response);

    const { status, data = {} } = response;
    const { returncode } = data;
    if (status === 200 && returncode === 1) {
      console.log("response", data);
      return data;
    }
    // console.log("resp", response);
    return Promise.reject(data);
  },
  function(error) {
    // Do something with response error
    console.log("error", error);
    return Promise.reject(error);
  }
);

const BASE_API = "api/v1/paymenturl";

const createOrderURL = async params => {
  const sig = computeSig(config, params);
  const data = QueryString.stringify({
    ...params,
    sig
  });

  return axios.post(`${BASE_API}/order`, data);
};

const getOrderInfo = (paymentott, params) => {
  const data = QueryString.stringify(params);
  const url = `${BASE_API}/${paymentott}?${data}`;
  console.log("getOrderInfo", url);
  return axios.get(url);
};

const submitOrder = (paymentott, params) => {
  console.log("submitOrder", paymentott, params);

  const data = QueryString.stringify(params);
  return axios.post(`${BASE_API}/submit/${paymentott}`, data);
};

const getOrderStatus = (paymentott, params) => {
  const data = QueryString.stringify(params);
  return axios.get(`${BASE_API}/${paymentott}/status`, data);
};

export default {
  getOrderInfo,
  createOrderURL,
  submitOrder,
  getOrderStatus
};
