import { eventChannel, END } from "redux-saga";
import { take, call, put, takeEvery } from "redux-saga/effects";
import { ACTION_TYPE } from "../actions";

const LOCK_SECONDS = 15 * 60;

function decreaseCountdown(secs, emitter) {
  secs -= 1;
  if (secs > 0) {
    emitter(secs);
  } else {
    // this causes the channel to close
    emitter(END);
  }
  return secs;
}

function countdown(secs) {
  return eventChannel(emitter => {
    secs = decreaseCountdown(secs, emitter);
    const iv = setInterval(() => {
      secs = decreaseCountdown(secs, emitter);
    }, 1000);
    // The subscriber must return an unsubscribe function
    return () => {
      clearInterval(iv);
    };
  });
}

function* watchCountdown(secs) {
  console.log("watchCountdown");
  const channel = yield call(countdown, secs);
  try {
    while (true) {
      // take(END) will cause the saga to terminate by jumping to the finally block
      let seconds = yield take(channel);
      console.log(`countdown: ${seconds}`);
      yield put({
        type: "COUNTDOWN_DECREASE",
        payload: seconds
      });
    }
  } finally {
    console.log("countdown terminated");
    yield put({
      type: "LOCK_INPUT_PASSCODE",
      payload: false
    });
  }
}

function* countdownSaga() {
  yield takeEvery("LOCK_INPUT_PASSCODE", function*({ payload }) {
    if (payload) {
      yield watchCountdown(LOCK_SECONDS);
    }
  });
}

export default countdownSaga;
