import ACTION_TYPE from "../actions/action-type";

import {
  SOURCE_MONEY_ID,
  INPUT_PASSCODE_MODE,
  PMC_ID,
  PMC_IMAGE
} from "../constants";

const initialState = {
  lockInputPasscode: false,
  passcodeError: "",
  passcode: "",
  showInputPasscode: false,
  countdown: ""
};

export default (state = initialState, action = {}) => {
  const { type = "", payload } = action;
  switch (type) {
    case "LOCK_INPUT_PASSCODE":
      return {
        ...state,
        lockInputPasscode: !!payload,
        passcode: ""
      };
    case "UPDATE_INPUT_PASSCODE_ERROR":
      return {
        ...state,
        passcodeError: payload || "",
        passcode: ""
      };
    case "INPUT_PASSCODE":
      return {
        ...state,
        showInputPasscode: payload,
        passcodeError: ""
      };
    case "UPDATE_PASSCODE":
      return {
        ...state,
        passcode: payload,
        passcodeError: ""
      };
    case "COUNTDOWN_DECREASE": {
      const secondsRemaining = payload;
      const minutes = Math.floor(secondsRemaining / 60);
      const seconds = secondsRemaining - minutes * 60;
      const convert = num => ("0" + num).slice(-2);
      return {
        ...state,
        countdown: `${convert(minutes)}:${convert(seconds)}`
      };
    }

    default:
      return state;
  }
};
