import ACTION_TYPE from "./action-type";
import { INPUT_PASSCODE_MODE } from "../constants";

const getOrderInfo = location => {
  return {
    type: ACTION_TYPE.GET_ORDER_INFO,
    payload: location
  };
};

const submitOrder = params => {
  return {
    type: ACTION_TYPE.SUBMIT_ORDER,
    payload: params
  };
};

const createOrderURL = params => {
  return {
    type: "CREATE_ORDER_URL",
    payload: params
  };
};

const changeSource = source => {
  return {
    type: "CHANGE_SOURCE",
    payload: source
  };
};

const changePasscode = passcode => {
  return {
    type: "UPDATE_PASSCODE",
    payload: passcode
  };
};

const showInputPasscode = show => {
  return {
    type: "INPUT_PASSCODE",
    payload: show
  };
};

const startup = params => {
  return {
    type: "STARTUP",
    payload: params
  };
};

export default {
  getOrderInfo,
  submitOrder,
  createOrderURL,
  changeSource,
  showInputPasscode,
  changePasscode,
  startup
};
export { ACTION_TYPE };
