import React from "react";

import { withRouter } from "react-router";
import PropTypes from "prop-types";

import { Button, Modal } from "react-bootstrap";

import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import Actions from "../../actions";

import ReactOtpInput from "../react-otp-input";

import { INPUT_PASSCODE_MODE } from "../../constants";

import "./index.scss";

const FORGOT_PASSWORD = "Quên mật khẩu";
const CONFIRM_INPUT_CODE_TITLE = "Nhập mật khẩu thanh toán của ZaloPay";

const LOCK_TITLE = "Mật khẩu thanh toán bị khoá tạm thời";

const LOCK_DESCRIPTION =
  "Bạn đã nhập sai quá 3 lần quy định. Để đảm bảo tài khoản được an toàn. Vui lòng thử lại sau.";

const TRY_LATER = "Thử Lại Sau";

const FORGOT_PASSWORD_BUTTON_TITLE = "Quên Mật Khẩu";

const NUMBER_OF_DIGITS = 6;

class ZPPasscodeInputModal extends React.PureComponent {
  handleForgotPasswordPress = () => {
    const { history } = this.props;
    history.push(`/forgot-password`);
  };
  handleTryLaterPress = () => {
    this.props.onHide();
  };

  handleTextChange = event => {
    const code = event.target.value;
    const { actions, onInputCompleted } = this.props;
    actions.changePasscode(code);
    if (code.length === NUMBER_OF_DIGITS) {
      onInputCompleted && onInputCompleted(code);
    }
  };

  renderInput() {
    const {
      passcodeError = "",
      onHide,
      onInputCompleted,
      passcode
    } = this.props;
    return (
      <div className="input">
        <button className="close-button" onClick={onHide}>
          <img src={require("../../resources/ic-hide-popup.svg")} alt="" />
        </button>
        <p className="confirm-input-code-title">{CONFIRM_INPUT_CODE_TITLE}</p>
        <ReactOtpInput
          numberOfDigits={NUMBER_OF_DIGITS}
          onChange={this.handleTextChange}
          value={passcode}
        />
        <p className="error">{passcodeError}</p>

        <a className="forgot-password" href={`forgot-password`}>
          {FORGOT_PASSWORD}
        </a>
      </div>
    );
  }

  renderLock() {
    const { countdown, onHide } = this.props;
    return (
      <div className="lock">
        <img src={require("../../resources/art-check-data.svg")} alt="" />

        <p className="title">{LOCK_TITLE}</p>
        <p className="description">{LOCK_DESCRIPTION}</p>
        {!!countdown && (
          <p>
            {"Mở lại sau "}
            <span className="count-down">{countdown}</span>
          </p>
        )}

        <p className="buttons">
          <button onClick={onHide}> {TRY_LATER}</button>
          <button
            className="normal-button"
            onClick={this.handleForgotPasswordPress}
          >
            {FORGOT_PASSWORD_BUTTON_TITLE}
          </button>
        </p>
      </div>
    );
  }

  render() {
    const { inputMode, lockInputPasscode, ...otherProps } = this.props;
    if (lockInputPasscode) {
      return <Modal {...otherProps}>{this.renderLock()}</Modal>;
    }
    return <Modal {...otherProps}>{this.renderInput()}</Modal>;
  }
}

ZPPasscodeInputModal.propTypes = {
  inputMode: PropTypes.oneOf(Object.values(INPUT_PASSCODE_MODE))
};

const mapStateToProps = state => ({
  ...state.passcode
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(Actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(ZPPasscodeInputModal));
