import { put, takeEvery, all, call } from "redux-saga/effects";
import queryString from "query-string";
import { push } from "connected-react-router";

import {
  ERROR_CODE,
  TRANSACTION_STATUS,
  AUTH_INVALID_MESSAGE
} from "../constants";

import ACTION_TYPE from "../actions/action-type";
import API from "../api";
import countdownSaga from "./countdown";

import Helper from "../helper";

function* submitOrder({ payload }) {
  try {
    const { location, pin } = payload;

    console.log("location", location);

    const { pathname, search } = location;

    const { appid, oauthtoken, paymentott } = queryString.parse(search);

    const apptime = new Date().getTime();

    const { data } = yield API.submitOrder(pathname, {
      appid,
      oauthtoken,
      pin,
      chargeinfo: "",
      apptime: apptime
    });
    console.log("submitOrder", data);

    yield push("./result", data);
  } catch (error) {
    console.log("submitOrder error", error);
    const { returncode, returnmessage } = error;
    if (returncode === ERROR_CODE.LOCK_INPUT_PASSCODE) {
      yield put({
        type: "LOCK_INPUT_PASSCODE",
        payload: true
      });
    }
    if (returncode === ERROR_CODE.PROCESSING) {
      /* The transaction is being processed */
      yield put({
        type: "UPDATE_TRANSACTION_STATUS",
        payload: TRANSACTION_STATUS.PROCESSING
      });
      yield put(push("/result"));
      return;
    }

    yield put({
      type: "UPDATE_INPUT_PASSCODE_ERROR",
      payload: returnmessage
    });
  }
}

function* getOrderInfo({ payload }) {
  const { appid, oauthtoken, paymentott } = payload;
  if (!appid || !oauthtoken || !paymentott) {
    // yield put(push("error-page", { message: AUTH_INVALID_MESSAGE }));
    return;
  }
  try {
    const { data } = yield API.getOrderInfo(paymentott, { appid, oauthtoken });
    console.log("getOrderInfo", data);
    yield put({
      type: ACTION_TYPE.SET_ORDER_INFO,
      payload: data
    });
  } catch (error) {
    console.log("getOrderInfo", error);
    const { returnmessage /* , returncode */ } = error;
    // yield put(push("/error-page", { message: returnmessage }));
  }
}

function* startup({ payload }) {
  const { location, match } = payload;
  const { appid, oauthtoken, paymentott } = Helper.getParametersWithLocation(
    location,
    match
  );

  console.log("location, match", location, match);

  yield put({
    type: "SET_BASIC_INFO",
    payload: {
      appid,
      oauthtoken,
      paymentott
    }
  });
  yield call(getOrderInfo, { payload: { appid, oauthtoken, paymentott } });
}

function* mainSaga() {
  yield takeEvery("STARTUP", startup);
  yield takeEvery(ACTION_TYPE.SUBMIT_ORDER, submitOrder);
}

export default function* rootSaga() {
  yield all([mainSaga(), countdownSaga()]);
}
