import React from "react";

import "./otp.scss";

class ReactOtpInput extends React.PureComponent {
  state = {
    code: ""
  };
  handlePinTouch = () => {
    this.focus();
  };
  focus() {
    const input = document.getElementById("zp-input-digit");
    input.focus();
  }

  render() {
    const { numberOfDigits, value, onChange } = this.props;
    const dots = [];
    for (let index = 0; index < numberOfDigits; index++) {
      const active = value.length > index;
      const className = `digit ${active ? "digit-hover" : ""}`;
      dots.push({
        index,
        className
      });
    }

    return (
      <div className="opt-input-wrapper">
        <input
          id="zp-input-digit"
          onChange={onChange}
          maxLength={numberOfDigits}
          pattern="[0-9]*"
          inputMode="decimal"
          className="text-input"
          type="password"
          value={value}
        />
        <div className="dot-wrapper" onClick={this.handlePinTouch}>
          {dots.map(({ className }, index) => (
            <div className={className} key={index} />
          ))}
        </div>
      </div>
    );
  }
}
ReactOtpInput.defaultProps = {
  numberOfDigits: 7
};

export default ReactOtpInput;
