import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Button } from "react-bootstrap";

import Actions from "../../actions";
import { ItemList } from "../../components";

import "./index.scss";

import {
  TRANSACTION_STATUS_ICON,
  TRANSACTION_STATUS,
  RESULT_TRANSACTION_STATUS_TITLE
} from "../../constants";
import { Amount } from "../../components";

const CLOSE_BUTTON_TITLE = "Đóng";

const RESULT_BOTTOM_TITLE = "Tải ZaloPay để xem lại các giao dịch.";

class ResultPage extends React.PureComponent {
  componentDidMount() {
    const { actions } = this.props;
    actions.getOrderInfo();
  }
  render() {
    const { amount, description, items, transactionStatus } = this.props;

    return (
      <div className="page result-page">
        <img
          className="status-image"
          src={TRANSACTION_STATUS_ICON[transactionStatus]}
          alt=""
        />
        <Amount amount={amount} />
        <p className="title status-title">
          {RESULT_TRANSACTION_STATUS_TITLE[transactionStatus]}
        </p>
        <p className="small-description-text description">{description}</p>

        <ItemList topItems={items} bottomItems={items} />

        <div className="footer">
          <Button
            className="close-button"
            variant="success"
            onClick={this.confirmButtonClick}
          >
            {CLOSE_BUTTON_TITLE}
          </Button>
          <p className="small-highlight-text description">
            {RESULT_BOTTOM_TITLE}
          </p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  ...state.orderInfo,
  ...state.paymentInfo,
  orderStatus: TRANSACTION_STATUS.PROCESSING
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(Actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ResultPage);
