import React from "react";
import "./index.scss";

const OrderItem = ({ title, description }) => (
  <p className="order-item">
    <span className="order-item-title">{title}</span>
    <span className="order-item-description">{description}</span>
  </p>
);

const ItemList = ({ topItems, bottomItems, showBottomDash = true }) => (
  <div className="item-list">
    {!!topItems &&
      topItems.map((item, index) => (
        <OrderItem {...item} key={index.toString()} />
      ))}
    {!!topItems && !!bottomItems && <div className="dash" />}
    {!!bottomItems &&
      bottomItems.map((item, index) => (
        <OrderItem {...item} key={index.toString()} />
      ))}
    {!!showBottomDash && <div className="dash" />}
  </div>
);

export default ItemList;
